import React, { useState } from 'react'
import './style.css'
import Modal from '@mui/material/Modal';
import CancelRoundedIcon from '@mui/icons-material/CancelRounded';
import CircularProgress from '@mui/material/CircularProgress';
import { saveName } from '../../Food/firebaseHandlers';

export default function Addmodal({ type }) {

    const [isModal, setIsModal] = useState(false)
    const [inputHanlder, setInputHandler] = useState("")
    const [isLoading, setIsLoading] = useState("Something to add")

    return <div>
        <Modal
            open={isModal}
            onClose={() => null}
        >
            <main id='modal-add'>
                <aside onClick={() => setIsModal(false)}><CancelRoundedIcon /></aside>
                <p>STATUS : {isLoading == "pending" ? <CircularProgress /> : isLoading}</p>
                <input value={inputHanlder} onChange={e => {
                    setIsLoading("Typing......")
                    setInputHandler(e.target.value)
                }} placeholder={`Add ${type}`} />
                <button onClick={async () => {
                    setIsLoading('pending')
                    setTimeout(() => {
                        saveName({
                            collection: type,
                            toSave: inputHanlder
                        })
                            .then(() => {
                                setIsLoading("successfully added")
                                setInputHandler("")
                            }).catch(() => {
                                setIsLoading("Error")
                            })

                    }, 1300)
                }}>Save</button>
            </main>
        </Modal>
        <div className="modal-add-div" onClick={() => setIsModal(true)}>
            <p>Add</p>
            <p>{type}</p>
        </div>
    </div>
}
