import React from 'react'
import './style.css'
import { useNavigate } from 'react-router-dom'

export default function Home() {
    const navigate = useNavigate();
    return <main>
        <section id='home'>
            <h1>PLATOS SERVICES</h1>
            <section>
                <div>
                    <p onClick={() => {
                        navigate("person")
                    }}>Person list</p>
                    <h6>By Redux</h6>
                </div>

                <div>
                    <p onClick={() => {
                        navigate("food")
                    }}>Food list</p>
                    <h6>By Hooks</h6>
                </div>
            </section>
        </section>
        <h6 style={{ position: 'absolute', bottom: "4px", right: '6px', fontWeight: "700" }}>Rahul (+918448443891)</h6>
    </main>
}
