import React, { useEffect, useRef, useState } from 'react'
import SMheader from '../_component/header/header'
import AddmodalCompo from '../_component/Add-modal/Add-modal'
import { deleteUser, saveList, editUser } from '../../store/features/personList'
import { db } from '../../firebase'
import './style.css'
import { useSelector, useDispatch } from 'react-redux'
import EditIcon from '@mui/icons-material/Edit';
import CancelIcon from '@mui/icons-material/Cancel';
import MessageAlertCompo from '../_component/MessageAlertCompo/MessageAlertCompo'

export default function Person() {

    const dispatch = useDispatch()
    const { person } = useSelector(state => state.person)
    const [isEditable, setIsEditable] = useState("none")
    const [inputTemp, setInputTemp] = useState(null)
    const [messageAlert, setMessageAlert] = useState(null)

    useEffect(() => {
        const ref = db.collection('Person')
        return ref.onSnapshot(querySnapshot => {
            dispatch(saveList(querySnapshot.docs))
        })
    }, [])
    const alertMessageHandler = (message) => {
        setMessageAlert(message)
        setTimeout(() => {
            setMessageAlert(null)
        }, 1700);
    }
    return (
        <div className="main-body">

            <AddmodalCompo type="Person" />
            <SMheader pageTitle="Person" />


            {messageAlert && <MessageAlertCompo message={messageAlert ? messageAlert : "Saved"} />}

            <main id='person-page-list'>
                {person.map(val => {
                    return <div>
                        <input
                            className={val.data().id != isEditable ? "input-tobe-not-to-change" : "input-tobe-change"}
                            value={val.data().id != isEditable ? val.data().person : inputTemp}
                            onChange={(e) => {
                                setInputTemp(e.target.value)
                            }}
                            disabled={val.data().id != isEditable}
                        />
                        <aside
                            onClick={() => {
                                setIsEditable(val.data().id)
                                setInputTemp(null)
                            }}> <EditIcon />
                        </aside>
                        <aside
                            onClick={() => {
                                dispatch(deleteUser({
                                    collection: "Person",
                                    id: val.data().id
                                })).then(() => {
                                    setIsEditable(null)
                                    alertMessageHandler("Removed")
                                }).catch(() => {
                                    alertMessageHandler("Error")
                                })
                            }}> <CancelIcon />
                        </aside>
                        {(inputTemp &&
                            val.data().id == isEditable && inputTemp != val.data().person)
                            && <p onClick={() => {
                                dispatch(editUser({
                                    collection: 'Person',
                                    id: val.data().id,
                                    person: inputTemp
                                }))
                                    .then(() => {
                                        setIsEditable(null)
                                        alertMessageHandler("Updated")
                                    }).catch(() => {
                                        alertMessageHandler("Error")
                                    })
                            }}>Save changes</p>}
                    </div>
                })}
            </main>
        </div>
    )
}
